html {
  height: 100%;
}

body {
  background: #e1e4e6;
  font-family: "Montserrat", "Helvetica Neue", Arial, "Hiragino Kaku Gothic ProN", "Hiragino Sans", "メイリオ", sans-serif;
  font: 14px;
  margin: 0;
  padding: 0;
  height: 100%;
}

/*--------------------------------
位置調整
---------------------------------*/

#formAlign {
  text-align: center;
}

/*--------------------------------
インプットボタン
---------------------------------*/
.button {
  font-family: "メイリオ";
  position: absolute;
  left: 50%;
  top: 115px;
  margin-top: 50px;
  margin-left: -100px;
  display: inline-block;
  /* 文字サイズ */
  font-size: 12pt;
  /* 文字の太さ */
  font-weight: bold;
  /* 文字位置   */
  text-align: center;
  /* カーソル   */
  cursor: pointer;
  /* 余白       */
  padding: 10px 19px;
  /* 背景色     */
  background: #386b88;
  /* 文字色     */
  color: #ffffff;
  /* 1行の高さ  */
  line-height: 1em;
  /* なめらか変化 */
  transition: .3s;
  /* 影の設定 */
  box-shadow: 2px 2px 3px #666666;
  /* 枠の指定 */
  border: 2px solid #386b88;
}

.button:hover {
  /* カーソル時の影消去 */
  box-shadow: none;
  /* 背景色     */
  color: #386b88;
  /* 文字色     */
  background: #ffffff;
}

/*--------------------------------
テキストフォーム
---------------------------------*/

.textArea {
  font-family: "メイリオ";
  margin-top: 110px;
  margin-bottom: 50px;
  /* 入力域の最大幅 */
  width: 250px;
  /* 入力域の背景色 */
  background: #ffffff;
  /* 入力域の枠線   */
  border: 1px solid #1e6bb9;
  /* 入力域の角丸   */
  border-radius: 4px;
  /* 入力文字の余白 */
  padding: 8px;
  /* 入力文字サイズ */
  font-size: 14px;
  /* 入力文字の色   */
  color: #333333;
  /* 入力文字の太字 */
  font-weight: bold;
  /* 入力文字の間隔 */
  letter-spacing: .1em;
  /* 入力文字の間隔 */
  line-height: 18px;
  resize: horizontal;
}

/*--------------------------------
ヘッダー
---------------------------------*/

header {
  width: 100%;
  padding: 1px 0;
  background: #c4cbce;
}

.header-logo {
  font-size: 20px;
  font-weight: bold;
  color: #333333;
  line-height: 1;
  margin-left: 50px;
  letter-spacing: .05em;
}

/*--------------------------------
フッター
---------------------------------*/
.footer {
  position: sticky;
  top: 100vh;
  width: 100%;
  text-align: center;
  padding: 15px 0;
  background-color: #333;
}

.copyright {
  font-size: 12px;
  text-align: center;
  color: #797979;
}

/*--------------------------------
ポップアップ
---------------------------------*/

.popup_wrap input {
  display: none;
}

.popup_overlay {
  display: flex;
  justify-content: center;
  overflow: auto;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 999;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.7);
  opacity: 0;
  transition: opacity 0.5s, transform 0s 0.5s;
  transform: scale(0);
}

.popup_trigger {
  position: absolute;
  width: 100%;
  height: 100%;
}

.popup_content {
  position: relative;
  align-self: center;
  top: -50px;
  width: 90%;
  max-width: 850px;
  padding: 30px 40px;
  box-sizing: border-box;
  background: #fff;
  line-height: 1.4em;
  transition: 0.5s;
  text-align: justify;
}

.popup-logo {
  font-size: 18px;
  font-weight: bold;
  color: #333333;
  line-height: 1;
  letter-spacing: .05em;
}

.close_btn {
  position: absolute;
  top: 10px;
  right: 10px;
  font-size: 14px;
  cursor: pointer;
  padding: 8px;
  background: #f29c9f;
  color: #fff;
  border-radius: 15px;
  line-height: 14px;
  font-weight: bold;
}

.popup_wrap input:checked~.popup_overlay {
  opacity: 1;
  transform: scale(1);
  transition: opacity 0.5s;
}

.popup_wrap input:checked~.popup_overlay .popup_content {
  transform: translateY(50px);
}

.open_btn {
  position: relative;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 44px;
  height: 25px;
  margin-top: 20px;
  margin-left: 30px;
  color: rgb(255, 255, 255);
  background: #6688a8;
  padding: 20px 16px;
  border: solid 1px rgb(100, 103, 121);
  font-size: 14px;
  font-weight: bold;
  border-radius: 21px;
  cursor: pointer;
  transition: .3s ease;
}

.open_btn:hover {
  background: #fff;
  border: solid 1px rgb(100, 103, 121);
  color: rgb(51, 45, 45);
  transition: .3s ease;
}